<script lang="ts" setup>
import { computed } from 'vue'
import Time from '~/core/Time'

const props = defineProps<{
  allowedTime: number
  elapsedTime: number
}>()

const remainingMinutes = computed(() =>
  Time.formatTimeToString(remainingTime.value.minutes),
)
const remainingSeconds = computed(() =>
  Time.formatTimeToString(remainingTime.value.seconds),
)

const percentageValue = computed(() => {
  if (props.allowedTime == 0) return 0
  if (props.elapsedTime > props.allowedTime) return 0
  return props.elapsedTime == 0
    ? 100
    : 100 - Number(((props.elapsedTime / props.allowedTime) * 100).toFixed(0))
})

const remainingTime = computed(() => {
  return Time.getTimeFromSeconds(
    Math.round(props.allowedTime - props.elapsedTime),
  )
})

const progressStyle = computed(() => {
  if (percentageValue.value >= 0 && percentageValue.value <= 30)
    return 'border-error text-error'
  if (percentageValue.value > 30 && percentageValue.value <= 60)
    return 'border-warning text-warning'
  if (percentageValue.value > 60) return 'border-accent text-accent'

  return 'border-accent text-accent'
})
</script>

<script lang="ts">
export default {
  name: 'TimeDonutChart',
}
</script>

<template>
  <div
    name="donutChart"
    class="radial-progress border min-w-6"
    :class="progressStyle"
    :style="[`--value: ${percentageValue}; --size: 6rem; --thickness: 4px`]"
  >
    <p class="text-xs m-0 text-center">Time left</p>
    <p class="text-center font-bold m-0">
      <span data-testid="maxTimeAllowedInHoursView">{{
        remainingTime.hours
      }}</span
      >:<span data-testid="maxTimeAllowedInMinutesView">{{
        remainingMinutes
      }}</span
      >:<span data-testid="maxTimeAllowedInSecondsView">{{
        remainingSeconds
      }}</span>
    </p>
  </div>
</template>
