import { App } from 'vue'
import API from '~/core/CoreAPI'
import locales from '~/public/_locales/en/messages.json'

// All languages will have to match en-US
export type MessageSchema = keyof typeof locales
export type t = (
  first: MessageSchema,
  substitutions?: string | string[],
) => ReturnType<typeof chrome.i18n.getMessage>

export function createI18n(): {
  install: (app: App) => void
} {
  return {
    install(app) {
      const globalReleaseHandler: ReturnType<typeof injectGlobalFields> =
        injectGlobalFields(app)

      // release global scope
      const unmountApp = app.unmount
      app.unmount = () => {
        globalReleaseHandler && globalReleaseHandler()
        unmountApp()
      }
    },
  }
}

// Mimic i18n plugin to use $t in templates
// https://github.com/intlify/vue-i18n-next/blob/1bcf60586cc701d9699138b8de70ca66ae27fde0/packages/vue-i18n-core/src/i18n.ts#L1689
function injectGlobalFields(app: App): () => void {
  app.config.globalProperties.$t = t

  return () => {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    delete (app as any).config.globalProperties.$t
  }
}

export const t: t = function (first, substitutions) {
  return API.Chrome.Translation.get(first as string, substitutions)
}
