<script lang="ts" setup>
import BasePieChart, { PieChartData } from '../BasePieChart.vue'
import {
  ChartSource,
  useUsageSummary,
  WebsiteType,
} from '~/composables/useUsageSummary'
import { computed, ref } from 'vue'
import { TooltipItem } from 'chart.js'
import SessionSummaryListItem from '../SessionSummaryListItem.vue'
import { useDateRange } from '~/composables/useDateRange'
import DashboardHighlights from '~/components/DashboardHighlights.vue'
import DashboardInput from '~/components/DashboardInput.vue'

const activeChartSource = ref<ChartSource>('duration')
const activeWebsiteType = ref<WebsiteType>('all')

const { range, formattedStartDate, formattedEndDate } = useDateRange()

const tooltipOptions = {
  getLabel: ({ dataIndex }: TooltipItem<'doughnut'>) => {
    return filteredBySearchText.value?.[dataIndex].primaryMetric
  },
}

const { filteredBySearchText, searchText } = useUsageSummary(
  range,
  activeWebsiteType,
  activeChartSource,
)

const chartData = computed<PieChartData>(() => {
  return {
    labels: filteredBySearchText.value?.map((d) => d.appId),
    series: [
      { values: filteredBySearchText.value?.map((d) => d.primaryMetricValue) },
    ],
  }
})

const isChartDataEmpty = computed(() => {
  return chartData.value.series[0].values.length === 0
})
</script>

<template>
  <div>
    <div class="prose flex flex-col">
      <h1>{{ $t('dashboard') }}</h1>
      <dashboard-highlights />
    </div>

    <div class="flex flex-col gap-3 mt-3">
      <dashboard-input
        v-model:activeChartSource="activeChartSource"
        v-model:activeWebsiteType="activeWebsiteType"
        v-model:searchText="searchText"
        v-model:range="range"
        :formatted-start-date="formattedStartDate"
        :formatted-end-date="formattedEndDate"
      />
      <div
        class="border border-base-300 w-full h-[300px] p-4 flex justify-center items-center"
      >
        <p
          v-if="isChartDataEmpty"
          class="text-center"
          data-testid="no-chart-data"
        >
          {{ $t('noChartData') }}
        </p>
        <base-pie-chart
          v-else
          :data="chartData"
          :tooltip="tooltipOptions"
        />
      </div>

      <ul
        v-if="filteredBySearchText?.length > 0"
        class="list-none flex flex-col gap-4 mt-4"
      >
        <session-summary-list-item
          v-for="item in filteredBySearchText"
          :key="item.appId"
          :app-id="item.appId"
          :display-name="item.appId"
          :primary-metric="item.primaryMetric"
          :secondary-metric="item.secondaryMetric"
          :tertiary-metric="item.tertiaryMetric"
          :primary-metric-integer-percent="item.primaryMetricIntegerPercent"
          :group="item.group"
        />
      </ul>
    </div>
  </div>
</template>
