<script lang="ts" setup>
import { computed } from 'vue'
import SfFavicon from './SfFavicon.vue'
import { BlockGroup } from '../core/BlockedGroupsRepository'
import Time from '../core/Time'

const props = defineProps<{
  appId: string
  displayName?: string
  primaryMetric: string
  secondaryMetric: string
  tertiaryMetric: string
  primaryMetricIntegerPercent: number
  group?: BlockGroup | null
}>()

const appName = computed(() => {
  if (props.displayName === undefined) return props.appId
  return props.displayName
})

const color = computed<string | undefined>(() => 'hsl(var(--sc)/0.5)')
const progressBarStyle = computed(() => {
  return `background-color: ${color.value}; right: ${
    100 - props.primaryMetricIntegerPercent
  }%`
})

const borderClass = computed<string>(() => {
  if (!props.group) return 'border-base-300'
  if (props.group.elapsedTime < props.group.maxTimeAllowed * 0.5)
    return 'border-success'
  if (props.group.elapsedTime < props.group.maxTimeAllowed * 0.75)
    return 'border-warning'
  return 'border-error'
})

const textClass = computed<string>(() => {
  if (!props.group) return 'text-base-300'
  if (props.group.elapsedTime < props.group.maxTimeAllowed * 0.5)
    return 'text-success'
  if (props.group.elapsedTime < props.group.maxTimeAllowed * 0.75)
    return 'text-warning'
  return 'text-error'
})

const remainingDuration = computed(() => {
  if (!props.group) return ''
  return Time.formatDurationInSeconds(
    Math.max(props.group.maxTimeAllowed - props.group.elapsedTime, 0),
  )
})

const allowedDuration = computed(() => {
  if (!props.group) return ''
  return Time.formatDurationInSeconds(props.group.maxTimeAllowed)
})
</script>

<template>
  <li class="px-4 rounded-md ring-hover flex mt-6">
    <div class="dropdown dropdown-hover dropdown-right dropdown-end">
      <div class="mr-4 flex-shrink-0 w-10 h-10">
        <div
          data-testid="app-icon-wrapper"
          class="bg-white bg-opacity-divider rounded-full p-2 relative border-2"
          :class="borderClass"
        >
          <sf-favicon
            :app-id="props.appId"
            class="icon"
          />
        </div>
      </div>

      <div
        v-if="group"
        tabindex="0"
        class="dropdown-content z-[1] card card-compact w-48 p-1 shadow-xl bg-base-100 text-base-content"
      >
        <div class="card-body">
          <div class="flex flex-col mb-1 items-start">
            <p class="text-xs font-normal m-0 text-base-content/50">
              {{ $t('belongsTo') }}
            </p>
            <h3 class="text-lg font-bold">
              {{ group.groupName }}
            </h3>
          </div>

          <div class="flex flex-col gap-2">
            <div>
              <p class="text-xs m-0 text-base-content/50">
                {{ $t('totalAllowedTime') }}
              </p>
              <p class="text-lg font-bold">
                {{ allowedDuration }}
              </p>
            </div>
          </div>
          <div>
            <p class="text-xs m-0 text-base-content/50">
              {{ $t('timeLeft') }}
            </p>
            <p
              class="text-lg font-bold"
              :class="textClass"
            >
              {{ remainingDuration }}
            </p>
          </div>
        </div>
      </div>
    </div>

    <div class="flex-1 flex flex-col min-w-0">
      <div class="flex flex-row space-x-4 justify-between items-end">
        <span
          data-testid="app-name"
          class="font-medium text-on-background text-opacity-primary truncate"
        >
          {{ appName }}
        </span>
        <p
          class="font-mono text-opacity-primary pb-0.5 text-sm flex-shrink-0"
          data-testid="primary-metric"
        >
          {{ primaryMetric }}
        </p>
      </div>

      <div class="flex flex-row space-x-4 items-center">
        <div class="relative h-2 flex-1 bg-black bg-opacity-30 rounded-xl">
          <div
            class="absolute left-0 top-0 bottom-0 rounded-xl transition-all ease-linear bg-opacity-60"
            :style="progressBarStyle"
          />
        </div>
        <p
          class="text-opacity-secondary opacity-0 transition-opacity font-mono w-9 text-right text-sm"
          :class="{ 'opacity-100': !!color }"
        >
          {{ primaryMetricIntegerPercent }}%
        </p>
      </div>

      <p
        class="flex items-center gap-2 text-on-background text-opacity-secondary text-xs"
      >
        <span>{{ secondaryMetric }}</span>
        <!-- eslint-disable-next-line @intlify/vue-i18n/no-raw-text -->
        <span>&bull;</span>
        <span>{{ tertiaryMetric }}</span>
      </p>
    </div>
  </li>
</template>

<style scoped>
@keyframes spin {
  0% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(540deg);
    transform: rotate(540deg);
  }
}

.animated-spinner {
  animation: spin 1s cubic-bezier(0.4, 0.6, 0.6, 0.4) infinite;
  border-top: 2px solid rgba(255, 255, 255, 0.7);
}

a {
  @apply no-underline;
}
</style>
