import GeneralUtils from './GeneralUtils'

const log = GeneralUtils.createScopedLog('Layout')

export default class Layout {
  name: string | null = null
  connections: Record<string, any> = {}
  originator: string | null = null

  constructor(name: string) {
    this.name = name
    this.originator = name + ': Layout'
  }

  inject(model: Record<string, any>, view: Record<string, any>) {
    log(
      'inject',
      'Injecting ' +
        view.getClassName() +
        ' view into ' +
        this.name +
        ' layout',
    )

    const $container = this.getContainer(view)

    $container.html(view.render(model))
  }

  connect(view: Record<string, any>, $container: JQuery<HTMLElement>) {
    this.connections[view.getInstanceID()] = $container
  }

  getContainer(view: Record<string, any>) {
    return this.connections[view.getInstanceID()]
  }
}
