import { TimeInterval } from '../core/Time'

import { computed, Ref } from 'vue'
import { injectUsageClient } from '../utils/usage-proxy'
import { useQuery } from 'vue-query'
import { UsageHighlights } from '../utils/usage-database-client'

export function useUsageHighlights(
  options: Ref<{ date: number; interval: TimeInterval }>,
  client = injectUsageClient(),
) {
  const endDate = computed(() => options.value.date)
  const startDate = computed(() => endDate.value - options.value.interval)

  return useQuery(
    ['usage-highlights', startDate, endDate],
    async (): Promise<UsageHighlights> =>
      client.getUsageHighlights(startDate.value, endDate.value),
    {
      staleTime: 0,
      cacheTime: 0,
      keepPreviousData: true,
      placeholderData: () => ({
        blockedAppCount: 0,
        blockedSessionCount: 0,
        totalDuration: 0,
        appCount: 0,
        sessionCount: 0,
        favoriteHour: 0,
        adCount: 0,
      }),
    },
  )
}
