<script lang="ts" setup>
import { ref, computed, onMounted, toRaw } from 'vue'
import { API } from '~/core/API'
import TimeDonutChart from '~/components/BlockedGroups/TimeDonutChart.vue'
import AddBlockedWebsites from '~/components/BlockedGroups/AddBlockedWebsites.vue'
import DeleteGroupConfirm from '~/components/BlockedGroups/DeleteGroupConfirm.vue'
import Time from '~/core/Time'
import BaseModal from '~/components/Dialog/BaseDialog.vue'
import { isSetMaxTimeAllowed } from '~/utils/isSetMaxTimeAllowed'
import { BlockGroup } from '~/core/BlockedGroupsRepository'
import { useToast } from 'vue-toastification'
import SfFavicon from '~/components/SfFavicon.vue'

const props = defineProps<{
  group: BlockGroup
  isNuclearOptionEnabled: boolean
}>()

const emits = defineEmits<{
  (event: 'deleteGroup', groupName: string): void
  (
    event: 'updateGroup',
    sourceGroupName: string,
    groupName: string,
    maxTimeAllowed: number,
  ): void
}>()

const toast = useToast()

const groupName = ref<string>('')
const maxTimeAllowedInHours = ref<number>(0)
const maxTimeAllowedInMinutes = ref<number>(0)
const websites = ref<string[]>()
const allowedTime = ref<number>(0)
const elapsedTime = ref<number>(0)
const challengeRequired = ref<boolean>(false)

const isDefaultGroup = computed(() => props.group.groupName == 'Default')
const isWebsitesAdding = ref<boolean>(false)
const isGroupDeleting = ref<boolean>(false)
const hasUnsavedChanges = computed(
  () =>
    groupName.value != props.group.groupName ||
    maxTimeAllowedInHours.value != sourceMaxTimeAllowedInHours.value ||
    maxTimeAllowedInMinutes.value != sourceMaxTimeAllowedInMinutes.value,
)
const canChangeTime = computed(
  () =>
    !(timeExpired.value || challengeRequired.value) ||
    canChangeTimeForDefaultGroup.value,
)
const canDeleteGroup = computed(
  () =>
    !isDefaultGroup.value &&
    (!timeExpired.value || props.group.maxTimeAllowed == 0) &&
    !challengeRequired.value &&
    !props.isNuclearOptionEnabled,
)
const canDeleteWebsite = computed(
  () =>
    !props.isNuclearOptionEnabled &&
    !timeExpired.value &&
    !challengeRequired.value,
)

const canChangeTimeForDefaultGroup = computed(
  () => isDefaultGroup.value && props.group.maxTimeAllowed == 0,
)

const canSaveGroup = computed(
  () =>
    !(
      groupName.value == '' ||
      !hasUnsavedChanges.value ||
      timeExpired.value ||
      challengeRequired.value
    ) || canChangeTimeForDefaultGroup.value,
)
API.PubSub.listen('Challenge.complete', () => {
  challengeRequired.value = false
})

API.PubSub.listen(
  'Settings.data.refreshed',
  (message: string, payload: any) => {
    if (payload.changes.blockedGroups) {
      const groupsFromStorage = payload.changes.blockedGroups
        .newValue as BlockGroup[]
      const targetGroup = groupsFromStorage.find(
        (x) => x.groupName == props.group.groupName,
      )
      if (targetGroup != undefined) {
        elapsedTime.value = targetGroup.elapsedTime
      }
    }
  },
)

onMounted(() => {
  challengeRequired.value = API.Settings.get('challengeRequired') ?? false

  groupName.value = props.group.groupName
  maxTimeAllowedInHours.value = Time.getTimeFromSeconds(
    Math.round(props.group.maxTimeAllowed),
  ).hours
  maxTimeAllowedInMinutes.value = Time.getTimeFromSeconds(
    Math.round(props.group.maxTimeAllowed),
  ).minutes
  websites.value = props.group.websites
  allowedTime.value = props.group.maxTimeAllowed
  elapsedTime.value = props.group.elapsedTime
})

function validateHours(e: any) {
  if (e.target.value > 23) maxTimeAllowedInHours.value = 23
}

function validateMinutes(e: any) {
  if (e.target.value > 59) maxTimeAllowedInMinutes.value = 59
}

const remainingTime = computed(() => {
  return Time.getTimeFromSeconds(
    Math.round(allowedTime.value - elapsedTime.value),
  )
})

const sourceMaxTimeAllowedInHours = computed(
  () => Time.getTimeFromSeconds(Math.round(props.group.maxTimeAllowed)).hours,
)
const sourceMaxTimeAllowedInMinutes = computed(
  () => Time.getTimeFromSeconds(Math.round(props.group.maxTimeAllowed)).minutes,
)

const timeExpired = computed(
  () =>
    remainingTime.value.hours == 0 &&
    remainingTime.value.minutes == 0 &&
    remainingTime.value.seconds == 0,
)

async function saveGroup() {
  const summaryAllowedTime = Time.getTimeInSeconds(
    maxTimeAllowedInHours.value,
    maxTimeAllowedInMinutes.value,
  )
  if (
    isSetMaxTimeAllowed(
      props.group.maxTimeAllowed,
      props.group.elapsedTime,
      summaryAllowedTime,
    )
  ) {
    allowedTime.value = summaryAllowedTime
    await API.BlockedGroupsRepository.update(
      props.group.groupName,
      groupName.value,
      summaryAllowedTime,
      toRaw(websites.value ?? []),
    )
    emits(
      'updateGroup',
      props.group.groupName,
      groupName.value,
      summaryAllowedTime,
    ),
      toast.success(`Group ${props.group.groupName} has been saved`)
  } else {
    maxTimeAllowedInHours.value = Time.getTimeFromSeconds(
      Math.round(props.group.maxTimeAllowed),
    ).hours
    maxTimeAllowedInMinutes.value = Time.getTimeFromSeconds(
      Math.round(props.group.maxTimeAllowed),
    ).minutes
  }
}

async function addWebsites(isValid: boolean, targetWebsites: string[]) {
  if (isValid) {
    isWebsitesAdding.value = false
    websites.value = websites.value?.concat(targetWebsites)
    await API.BlockedGroupsRepository.updateWebsites(
      props.group.groupName,
      toRaw(websites.value ?? []),
    )
  }
}

async function deleteWebsite(website: string) {
  websites.value = websites.value?.filter((x) => x != website)
  await API.BlockedGroupsRepository.updateWebsites(
    props.group.groupName,
    toRaw(websites.value ?? []),
  )
}

async function deleteGroup() {
  isGroupDeleting.value = true
}

async function confirmDeleteGroup(flag: boolean) {
  isGroupDeleting.value = false
  if (flag) {
    await API.BlockedGroupsRepository.delete(props.group.groupName)
    emits('deleteGroup', props.group.groupName)
  }
}
</script>

<script lang="ts">
export default {
  name: 'EditGroup',
}
</script>

<template>
  <div
    class="shadow-[0px_0px_0px_1px_#E2E8F0] mt-4 rounded-md overflow-x-hidden"
    name="blockedGroupElement"
    :data-testid="groupName"
  >
    <div class="flex flex-row p-3">
      <time-donut-chart
        :allowed-time="allowedTime"
        :elapsed-time="elapsedTime"
      />
      <div class="pl-3 flex-1">
        <div class="mt-3 form-control">
          <label class="input-group">
            <span>Name</span>
            <input
              type="text"
              placeholder="Group name"
              :disabled="isDefaultGroup || challengeRequired || !canChangeTime"
              v-model="groupName"
              class="input input-bordered bg-white w-full"
            />
          </label>
        </div>
        <div
          class="mt-3 flex flex-row flex-wrap justify-start items-start md:items-center"
        >
          <p class="text-ellipsis pr-4">Max Time Allowed per Day:</p>
          <div
            class="join input-group flex-1 min-w-[200px] md:min-w-[250px] md:max-w-[320px]"
          >
            <input
              type="number"
              min="0"
              @input="validateHours"
              v-model="maxTimeAllowedInHours"
              data-testid="maxTimeAllowedInHours"
              :disabled="!canChangeTime"
              class="input input-bordered pr-0 input-sm join-item bg-white min-w-0 flex-1"
            />
            <span class="rounded-none">hr</span>
            <input
              type="number"
              min="0"
              max="59"
              @input="validateMinutes"
              v-model="maxTimeAllowedInMinutes"
              data-testid="maxTimeAllowedInMinutes"
              :disabled="!canChangeTime"
              class="input input-bordered pr-0 input-sm join-item bg-white min-w-0 flex-1"
            />
            <span class="rounded-none">min</span>
          </div>
        </div>
        <p
          data-testid="timeExpiredMsg"
          v-if="timeExpired"
          class="alert alert-error mt-2"
        >
          {{ $t('timeExpiredForGroup', [groupName]) }}
        </p>

        <button
          class="btn btn-primary btn-sm mt-3"
          @click="saveGroup()"
          data-testid="saveGroupBtn"
          :disabled="!canSaveGroup"
        >
          SAVE CHANGES
        </button>
        <button
          v-if="canDeleteGroup"
          @click="deleteGroup()"
          class="btn btn-outline btn-error btn-sm mt-3 not-prose float-right"
        >
          <iconify-icon
            icon="mdi:bin"
            width="24"
            class="align-text-bottom"
          />
          DELETE GROUP
        </button>
      </div>
    </div>
    <div class="p-3 mt-3 border-t-[1px]">
      <div class="w-full flex justify-between items-center gap-4">
        <p class="text-sm grow font-bold whitespace-nowrap">BLOCKED SITES</p>
        <button
          class="btn btn-sm btn-primary btn-outline shrink flex-nowrap justify-start whitespace-nowrap overflow-hidden text-ellipsis"
          @click="isWebsitesAdding = true"
          data-testid="addWebsitesBtn"
        >
          <iconify-icon
            icon="mdi:plus"
            width="24"
            class="inline shrink-0"
          />
          ADD BLOCKED SITES
        </button>
      </div>
      <div class="w-full not-prose">
        <ul
          class="list-none"
          name="blockedWebsites"
        >
          <li
            v-for="website in websites"
            :key="website"
          >
            <div class="flex justify-start items-center">
              <button
                class="btn btn-sm btn-ghost btn-circle mr-2"
                v-if="canDeleteWebsite"
                @click="deleteWebsite(website)"
              >
                <iconify-icon
                  icon="mdi:close"
                  class="cursor-pointer align-middle text-error"
                  data-testid="deleteWebsiteBtn"
                  data-url="website"
                />
              </button>
              <sf-favicon
                :app-id="website"
                :size="24"
                class="icon inline-block mr-2 align-middle w-6 h-6"
              />
              <p class="inline-block">{{ website }}</p>
            </div>
          </li>
        </ul>
      </div>

      <base-modal :visible="isGroupDeleting">
        <delete-group-confirm
          :group-name="groupName"
          @confirm="confirmDeleteGroup"
        />
      </base-modal>
      <base-modal :visible="isWebsitesAdding"
        ><add-blocked-websites
          @add-websites="addWebsites"
          @cancel="isWebsitesAdding = false"
        />
      </base-modal>
    </div>
  </div>
</template>
