import { computed, ref } from 'vue'
import { defineChartColors } from '@sensortower/chart-colors'
function percentToHex(percent: number) {
  return Math.floor((255 * percent) / 100)
    .toString(16)
    .padStart(2, '0')
}

const onBackgroundColor = '#000000'

const titleColor = computed(() => onBackgroundColor + percentToHex(90))
const textColor = computed(() => onBackgroundColor + percentToHex(70))
const gridColor = computed(() => onBackgroundColor + percentToHex(12))
const axisColor = computed(() => onBackgroundColor + percentToHex(70))

const colors = ref([
  '#ED3B3B',
  '#F48E2F',
  '#FAD61D',
  '#40DA99',
  '#4FE6E8',
  '#5080E7',
  '#854FE8',
])

export function useChartColors() {
  const getSeriesColor = computed(() => defineChartColors(colors.value))

  return {
    titleColor,
    textColor,
    gridColor,
    axisColor,
    getSeriesColor(index: number) {
      const color = getSeriesColor.value(index)
      return {
        borderColor: color,
        fillColor: color + percentToHex(25),
      }
    },
  }
}
