import API from '~/core/CoreAPI'
import View from './OnboardingOverlayView'
import Model from './OnboardingOverlayModel'

var log = API.Utils.createScopedLog('OnboardingOverlayController')

export default class OnboardingOverlayController {
  view = null
  model = null

  constructor() {
    log(
      'constructor',
      'Constructing OnboardingOverlayController',
      null,
      'DEBUG',
    )
    this.model = new Model()
    this.view = new View()
  }

  async init() {
    log('init', 'Initializing controller', null, 'DEBUG')
    await Promise.all([API.Settings.initAsync(), API.Requirements.init()])

    this.mountView()
  }

  async mountView() {
    const referrer = location.hostname + location.pathname
    // Show onboarding only on the options page
    if (!this.isOptionsPage()) return

    const isCompleted = await API.Requirements.isOnboardingCompleted()
    if (isCompleted) return

    this.view.init(referrer)
  }

  isExtensionPage() {
    return location.hostname === API.Chrome.Extension.getID()
  }
  isOptionsPage() {
    return this.isExtensionPage() && location.pathname === '/options.html'
  }
  isBlockedPage() {
    return location.hostname.endsWith('stayfocusd.com')
  }
}
