<script lang="ts" setup>
import { ref, computed, toRaw, onMounted } from 'vue'
import { API } from '~/core/API'
import TimeDonutChart from '~/components/BlockedGroups/TimeDonutChart.vue'
import Time, { MINUTE_IN_SEC } from '~/core/Time'
import { BlockGroup } from '~/core/BlockedGroupsRepository'
import AddBlockedWebsites from '~/components/BlockedGroups/AddBlockedWebsites.vue'
import { useToast } from 'vue-toastification'

const emits = defineEmits<{
  (event: 'saveGroup', group: BlockGroup): void
  (event: 'cancel'): void
}>()

const toast = useToast()

const defaultNewGroup: BlockGroup = {
  groupName: '',
  websites: [],
  elapsedTime: 0,
  maxTimeAllowed: MINUTE_IN_SEC * 10,
}

const groupNameElement = ref<HTMLDivElement>()
const groupName = ref<string>(defaultNewGroup.groupName)
const maxTimeAllowedInHours = ref<number>(
  Time.getTimeFromSeconds(Math.round(defaultNewGroup.maxTimeAllowed)).hours,
)
const maxTimeAllowedInMinutes = ref<number>(
  Time.getTimeFromSeconds(Math.round(defaultNewGroup.maxTimeAllowed)).minutes,
)
const websites = ref<string[]>()
const isGettingWebsites = ref<boolean>(false)

const allowedTime = computed(() =>
  Time.getTimeInSeconds(
    maxTimeAllowedInHours.value,
    maxTimeAllowedInMinutes.value,
  ),
)

onMounted(() => {
  groupNameElement.value?.focus()
})

function validateHours(e: any) {
  if (e.target.value > 23) maxTimeAllowedInHours.value = 23
}

function validateMinutes(e: any) {
  if (e.target.value > 59) maxTimeAllowedInMinutes.value = 59
}

async function saveGroup() {
  const newGroup = {
    groupName: groupName.value,
    elapsedTime: 0,
    websites: toRaw(websites.value == undefined ? [] : websites.value),
    maxTimeAllowed: Time.getTimeInSeconds(
      maxTimeAllowedInHours.value,
      maxTimeAllowedInMinutes.value,
    ),
  }
  const groupsFromStorage = API.BlockedGroupsRepository.get()
  if (
    groupsFromStorage.find(
      (gr) => gr.groupName.toLowerCase() == newGroup.groupName.toLowerCase(),
    ) != null
  ) {
    toast.error('This group name is already in use, choose a different name')
    return
  }
  await API.BlockedGroupsRepository.add(
    newGroup.groupName,
    newGroup.maxTimeAllowed,
    newGroup.websites,
    newGroup.elapsedTime,
  )
  toast.success(`Group ${newGroup.groupName} has been added`)
  emits('saveGroup', newGroup)
}

function addWebsites(isValid: boolean, targetWebsites: string[]) {
  isGettingWebsites.value = false
  if (isValid) {
    websites.value = targetWebsites
    saveGroup()
  }
}
</script>

<script lang="ts">
export default {
  name: 'NewGroup',
}
</script>

<template>
  <div
    class="rounded-md min-w-[20rem] max-w-[50rem]"
    name="blockedGroupElement"
  >
    <div class="flex flex-row p-3">
      <time-donut-chart
        :allowed-time="allowedTime"
        :elapsed-time="0"
      />
      <div class="pl-3 flex-1 overflow-hidden">
        <div class="mt-3 form-control">
          <label class="input-group">
            <span>Name</span>
            <input
              type="text"
              ref="groupNameElement"
              placeholder="Group name"
              v-model="groupName"
              class="input input-sm input-bordered bg-white w-full"
            />
          </label>
        </div>
        <div class="mt-3 flex flex-row justify-start items-center">
          <p>Max Time Allowed per Day:</p>
          <div class="join overflow-hidden input-group max-w-[370px]">
            <label class="input-group ml-3">
              <input
                type="number"
                min="0"
                v-model="maxTimeAllowedInHours"
                @input="validateHours"
                class="input input-bordered pr-0 input-sm join-item bg-white min-w-0 flex-1"
              />
              <span class="rounded-none">hr</span>
              <input
                type="number"
                min="0"
                max="59"
                v-model="maxTimeAllowedInMinutes"
                @input="validateMinutes"
                class="input input-bordered pr-0 input-sm join-item bg-white min-w-0 flex-1"
              />
              <span class="rounded-none">min</span>
            </label>
          </div>
        </div>
      </div>
    </div>
    <div class="p-3">
      <add-blocked-websites
        :is-new-group="true"
        :is-save-event="isGettingWebsites"
        @add-websites="addWebsites"
      />
      <div class="py-3 sm:flex sm:flex-row-reverse">
        <button
          class="btn btn-primary btn-sm float-right ml-2"
          @click="isGettingWebsites = true"
          :disabled="groupName == ''"
        >
          SAVE GROUP
        </button>
        <button
          type="button"
          class="btn btn-sm"
          @click="$emit('cancel')"
        >
          CANCEL
        </button>
      </div>
    </div>
  </div>
</template>
