<script lang="ts" setup>
import { ref, computed, watch, onMounted } from 'vue'
import { useToast } from 'vue-toastification'
import { API } from '~/core/API'

const props = defineProps<{
  isNewGroup?: boolean
  isSaveEvent?: boolean
}>()

const emits = defineEmits<{
  (event: 'addWebsites', isValid: boolean, websites: string[]): void
  (event: 'cancel'): void
}>()

const toast = useToast()
const STAYFOCUSD = 'stayfocusd.com'
const EXTENSION_PAGE = 'chrome-extension://'

type ExistingSite = {
  website: string
  groupName: string
}

watch(
  () => props.isSaveEvent,
  (value) => {
    if (value == true) addWebsites()
  },
)

const websitesElement = ref<HTMLDivElement>()
const websitesPlaneText = ref<string>('')
const websites = computed(() => {
  const websites = new Set<string>()
  const websitesArray = websitesPlaneText.value.split('\n')
  websitesArray.forEach((website) => {
    if (website != '') websites.add(website.toLowerCase())
  })
  return Array.from(websites)
})

onMounted(() => {
  websitesElement.value?.focus()
})

function addWebsites() {
  if (websites.value.find((site) => site.includes(STAYFOCUSD))) {
    toast.error(API.Chrome.Translation.get('cannotBlockStayFocusd'))
    return
  }

  let existingSites: ExistingSite[] = []
  websites.value.forEach((site) => {
    const group = API.BlockedGroupsRepository.getBlockedGroupByDomain(site)
    if (group != null) {
      existingSites.push({
        website: site,
        groupName: group.groupName,
      })
    }
  })

  if (existingSites.length > 0) {
    let existingSitesText = ''
    existingSites.forEach(
      (site) =>
        (existingSitesText += `Group ${site.groupName} inludes ${site.website} \n`),
    )
    toast.warning(
      `You have already added websites with these URLs to another groups \n${existingSitesText}`,
    )
    emits('addWebsites', false, [])
    return
  }

  emits('addWebsites', true, websites.value)
}

function addExtensionPage() {
  websitesPlaneText.value = websitesPlaneText.value.concat(
    `${EXTENSION_PAGE}\n`,
  )
}
</script>

<script lang="ts">
export default {
  name: 'AddBlockedWebsites',
}
</script>

<template>
  <div
    class="min-w-[20rem] max-w-[50rem] p-4 prose"
    data-testid="addSitesModal"
  >
    <div>
      <p class="text-sm w-52 inline-block font-bold">ADD BLOCKED SITES</p>

      <textarea
        v-model="websitesPlaneText"
        data-testid="newBlockedSites"
        ref="websitesElement"
        class="textarea textarea-bordered w-full h-32"
        placeholder="Enter blocked websites..."
      />
      <ul class="leading-tight mt-4">
        <li>
          To block multiple sites,
          <span class="font-bold">add one per line.</span>
        </li>
        <li>
          <span class="font-bold">Do not</span> type the http:// or https://
        </li>
        <li>
          If you want to block the entire site,
          <span class="font-bold">leave off the www</span> (i.e. cnn.com instead
          of www.cnn.com)
        </li>
        <li>
          You can use a
          <span class="font-bold">preceding asterisk as a wildcard</span>
          (i.e. *.com blocks all .com sites, or *foo blocks all domains with the
          word "foo" in them)
        </li>
      </ul>
      <p>
        Want to stop yourself from disabling StayFocusd?
        <a
          href="#"
          @click="addExtensionPage"
          data-analytics-click="CLICK_SETTINGS_BLOCK_EXTENSIONS_PAGE"
          >Block the Chrome Extensions page</a
        >!
      </p>
    </div>
    <div
      class="py-3 sm:flex sm:flex-row-reverse"
      v-if="!isNewGroup"
    >
      <button
        type="button"
        class="btn btn-primary btn-sm ml-2"
        @click="addWebsites"
        data-testid="addBlockedWebsitesToGroup"
      >
        ADD BLOCKED SITES
      </button>
      <button
        type="button"
        class="btn btn-sm"
        @click="$emit('cancel')"
      >
        CANCEL
      </button>
    </div>
  </div>
</template>
