import { driver } from 'driver.js'
import 'driver.js/dist/driver.css'
import '~/assets/driver.css'
import API from '~/core/CoreAPI'
import $ from 'jquery'

const Tutorial = {
  init() {
    const driverObj = driver({
      popoverClass: 'sf-driverjs',
      showProgress: true,
      steps: [
        {
          element: '[data-link="blockedSites"]',
          popover: {
            title: 'Blocked Sites',
            description:
              'This is where you add the websites you want to spend less time on. For each group of websites, you assign a max allowed time per day. When your time runs out for the day, StayFocusd will block you from accessing the websites in that group.',
            side: 'right',
            align: 'start',
            onPopoverRender: () => {
              API.Analytics.event('GUIDE_SHOW_BLOCKED_SITES')
            },
            onNextClick: () => {
              $('[data-link="blockedSites"]').click()
              driverObj.moveNext()
            },
          },
        },
        {
          element: '#blockAllSitesForGuide',
          popover: {
            description:
              'When enabled, all websites (other than ones listed in your "Allowed Sites") will count toward your max allowed time for the day.',
            side: 'bottom',
            align: 'start',
            onPopoverRender: () => {
              API.Analytics.event('GUIDE_SHOW_BLOCK_ALL_SITES')
            },
          },
        },
        {
          element: '[data-link="allowedSites"]',
          popover: {
            title: 'Allowed Sites',
            description: `Add websites or URLs here to prevent them from counting toward your max allowed time and to not block them after you've ran out of time.
            <br/>
            For example, if you add youtube.com as a blocked site, you can add any YouTube video URL as an allowed site to watch it after you've ran out of time for the day.`,
            side: 'bottom',
            align: 'start',
            onPopoverRender: () => {
              API.Analytics.event('GUIDE_SHOW_ALLOWED_SITES')
            },
          },
        },
        {
          element: '#activeTimeForGuide',
          popover: {
            title: 'Active Days & Active Hours',
            description:
              'You can configure which days of the week and which hours of the day you want StayFocusd to count down your time for blocked websites.',
            side: 'bottom',
            align: 'start',
            onPopoverRender: () => {
              API.Analytics.event('GUIDE_SHOW_ACTIVE_DAYS_AND_HOURS')
            },
          },
        },
        {
          element: '[data-link="nuclearOption"]',
          popover: {
            title: 'The Nuclear Option',
            description: `The Nuclear Option gives you the ability to immediately block websites without waiting for your time to run out. It's StayFocusd's most extreme method for blocking websites because it can't be turned off.
              <br/>
              Use it when you need to really focus and get something done.`,
            side: 'right',
            align: 'start',
            onPopoverRender: () => {
              API.Analytics.event('GUIDE_SHOW_NUCLEAR_OPTION')
            },
            onNextClick: () => {
              $('[data-link="nuclearOption"]').click()
              driverObj.moveNext()
            },
          },
        },
        {
          element: '#startAtScheduledTimeForGuide',
          popover: {
            description:
              'In addition to automatically enabling "The Nuclear Option" sometime in the future, you can also automatically enable it on a repeating schedule.',
            side: 'bottom',
            align: 'start',
            onPopoverRender: () => {
              API.Analytics.event('GUIDE_SHOW_NUCLEAR_OPTION_SCHEDULE')
            },
          },
        },
        {
          element: '[data-link="requireChallenge"]',
          popover: {
            title: 'Require Challenge',
            description: `This prevents you from breaking your own rules. When enabled, you'll have to type a very long paragraph without making a single mistake before changing settings. This prevents you from cheating by, for example, increasing the maximum time allowed, removing blocked sites, or adding new allowed sites.`,
            side: 'top',
            align: 'start',
            onPopoverRender: () => {
              API.Analytics.event('GUIDE_SHOW_REQUIRE_CHALLENGE')
            },
          },
        },
      ],
    })
    driverObj.drive()
  },
}

export default Tutorial
