<script lang="ts" setup>
import { ref } from 'vue'
import { API } from '~/core/API'
import BlockAllWebsites from '~/components/BlockedGroups/BlockAllWebsites.vue'
import BaseModal from '~/components/Dialog/BaseDialog.vue'
import EditGroup from '~/components/BlockedGroups/EditGroup.vue'
import NewGroup from '~/components/BlockedGroups/NewGroup.vue'
import { BlockGroup } from '~/core/BlockedGroupsRepository'

const groups = ref<BlockGroup[]>([])
const isNuclearOptionEnabled = ref<boolean>(false)
const isBlockAllWebsites = ref<boolean>()
const isGroupAdding = ref<boolean>(false)

API.Settings.init(async () => {
  await API.BlockedGroupsRepository.init()
  isNuclearOptionEnabled.value = getNuclearOption()
  let groupsFromStorage = API.BlockedGroupsRepository.get()
  groupsFromStorage = groupsFromStorage.sort((l, r) => {
    if (l.groupName === 'Default') return -1
    if (r.groupName === 'Default') return 1
    return 0
  })
  groups.value = groupsFromStorage.map((a) => {
    return { ...a }
  })
})

function onBlockAllWebsites(value: boolean) {
  isBlockAllWebsites.value = value
}

function onGroupSaved(group: BlockGroup) {
  isGroupAdding.value = false
  groups.value.push(group)
}

function onGroupDeleted(groupName: string) {
  const index = groups.value.findIndex((x) => x.groupName == groupName)
  if (index != undefined) groups.value.splice(index, 1)
}

function onGroupUpdated(
  sourceGroupName: string,
  groupName: string,
  maxTimeAllowed: number,
) {
  const group = groups.value.find((x) => x.groupName == sourceGroupName)
  if (group != undefined) {
    group.groupName = groupName
    group.maxTimeAllowed = maxTimeAllowed
  }
}

function getNuclearOption() {
  return (
    API.NuclearOption.isActive() &&
    API.NuclearOption.getBlockType() === 'blocked'
  )
}
</script>

<script lang="ts">
export default {
  name: 'BlockedGroups',
}
</script>

<template>
  <div class="prose">
    <h1>Blocked Sites</h1>
    <p>
      You are given a certain amount of time each day for a group of websites.
      After your time has ran out for a group, you will not be able to access
      any of that group’s websites during the active days & hours until the
      daily reset time.
    </p>
    <block-all-websites @block-all-websites="onBlockAllWebsites" />
    <div :class="{ 'pointer-events-none opacity-50': isBlockAllWebsites }">
      <div class="flex flex-row justify-between mt-4 items-center">
        <p class="font-bold text-lg">Groups</p>
        <button
          class="btn btn-primary btn-sm"
          @click="isGroupAdding = true"
        >
          Add group
        </button>
      </div>
      <p
        v-if="isNuclearOptionEnabled"
        class="alert alert-error"
      >
        {{
          API.Chrome.Translation.get(
            'cannotRemoveBlockedSiteDuringNuclearOption',
          )
        }}
      </p>
      <edit-group
        v-for="group in groups"
        :key="group.groupName"
        :group="group"
        :is-nuclear-option-enabled="isNuclearOptionEnabled"
        @delete-group="onGroupDeleted"
        @update-group="onGroupUpdated"
      />
    </div>

    <base-modal :visible="isGroupAdding">
      <new-group
        @save-group="onGroupSaved"
        @cancel="isGroupAdding = false"
      />
    </base-modal>
  </div>
</template>
