<script lang="ts" setup>
import { computed } from 'vue'

const props = defineProps<{
  appId: string
  size?: 24 | 48 | 96
}>()

const googleS2url = computed(
  () =>
    `https://www.google.com/s2/favicons?domain=https://${props.appId}&sz=${
      props.size ?? 48
    }`,
)
</script>

<template>
  <img
    :src="googleS2url"
    :alt="`Favicon for ${appId}`"
  />
</template>
