import Time, { DAY } from '~/core/Time'
import { computed, ref } from 'vue'
import DateUtils from '../core/DateUtils'
import { t } from '~/utils/i18n'

export interface DateInterval {
  startDate: number
  endDate: number
}

export function useDateRange() {
  const range = ref<DateInterval>({
    startDate: Time.getToday(),
    endDate: Time.getToday() + DAY,
  })

  const formattedStartDate = computed(() => {
    const startDisplay = DateUtils.timestampToDisplayDate(range.value.startDate)
    return `${startDisplay.month}/${startDisplay.day}/${startDisplay.year}`
  })

  const formattedEndDate = computed(() => {
    const todayDisplay = DateUtils.timestampToDisplayDate(Time.getToday())
    const endDisplay = DateUtils.timestampToDisplayDate(
      range.value.endDate - DAY,
    )
    const isToday =
      todayDisplay.year === endDisplay.year &&
      todayDisplay.month === endDisplay.month &&
      todayDisplay.day === endDisplay.day

    return isToday
      ? t('today')
      : `${endDisplay.month}/${endDisplay.day}/${endDisplay.year}`
  })

  return { range, formattedStartDate, formattedEndDate }
}
