import Time, { SECOND } from '../core/Time'
import API from '~/core/CoreAPI'
import { computed, ref, Ref } from 'vue'
import { injectUsageClient } from '~/utils/usage-proxy'
import { useQuery } from 'vue-query'
import { UsageSummary } from '~/utils/usage-database-client'
import { t } from '../utils/i18n'
import { BlockGroup } from '../core/BlockedGroupsRepository'
import { DateInterval } from './useDateRange'

export type ChartSource = 'duration' | 'ads' | 'sessions'
export type WebsiteType = 'all' | 'blocked' | 'allowed'
export type FilteredUsageSummary = UsageSummary &
  UsageListData & { group: BlockGroup | undefined }

interface UsageListData {
  primaryMetric: string
  primaryMetricValue: any
  secondaryMetric: string
  primaryMetricIntegerPercent: number
  tertiaryMetric: string
}

export function useUsageSummary(
  options: Ref<DateInterval>,
  activeWebsiteType: Ref<WebsiteType>,
  activeChartSource: Ref<ChartSource>,
  client = injectUsageClient(),
) {
  const searchText = ref<string>('')

  const { data: usageSummaryData, error } = useQuery(
    ['usage-summary', options],
    async (): Promise<UsageSummary[]> =>
      client.getUsageSummary(options.value.startDate, options.value.endDate),
    {
      keepPreviousData: true,
      placeholderData: () => [],
    },
  )

  const filteredByWebsiteType = computed<UsageSummary[]>(() => {
    const data = usageSummaryData.value ?? []
    switch (activeWebsiteType.value) {
      case 'all':
        return data
      case 'allowed':
        return data.filter((d) => d.allowed)
      case 'blocked':
        return data.filter((d) => d.blocked)
    }
    return data
  })

  const totalDuration = computed(() => {
    return (
      filteredByWebsiteType.value?.reduce(
        (acc, d) => acc + d.totalDuration,
        0,
      ) ?? 0
    )
  })

  const totalAdCount = computed(() => {
    return (
      filteredByWebsiteType.value?.reduce((acc, d) => acc + d.adCount, 0) ?? 0
    )
  })

  const totalSessionCount = computed(() => {
    return (
      filteredByWebsiteType.value?.reduce(
        (acc, d) => acc + d.sessionCount,
        0,
      ) ?? 0
    )
  })

  const sortedData = computed<UsageSummary[]>(() => {
    // don't mutate the original data
    const copy = filteredByWebsiteType.value?.slice()
    switch (activeChartSource.value) {
      case 'duration':
        return copy.sort((a, b) => b.totalDuration - a.totalDuration)
      case 'ads':
        return (
          copy
            .filter((d) => d.adCount != 0)
            .sort((a, b) => b.adCount - a.adCount) ?? []
        )
      case 'sessions':
        return copy.sort((a, b) => b.sessionCount - a.sessionCount)
    }
    return []
  })

  const data = computed(() => {
    return sortedData.value.map((d) => {
      const listData = getListData(
        activeChartSource.value,
        d,
        totalDuration.value,
        totalAdCount.value,
        totalSessionCount.value,
      )
      return {
        ...d,
        ...listData,
        group: API.BlockedGroupsRepository.getBlockedGroupByDomain(d.appId),
      }
    })
  })

  const filteredBySearchText = computed(() => {
    if (!searchText.value) return data.value

    const search = searchText.value.toLowerCase()
    return data.value?.filter((d) => d.appId.toLowerCase().includes(search))
  })

  return {
    filteredBySearchText,
    error,
    searchText,
  }
}

function formatSessions(sesionCount: number): string {
  return t('sessionCount', `${sesionCount}`)
}

function formatAds(adCount: number): string {
  return t('adCount', `${adCount}`)
}

function formatDuration(duration: number) {
  return Time.formatDurationInSeconds(duration / SECOND)
}

function getListData(
  chartSource: ChartSource,
  data: UsageSummary,
  totalDuration: number,
  totalAdCount: number,
  totalSessionCount: number,
): UsageListData {
  switch (chartSource) {
    case 'duration': {
      const total = totalDuration == 0 ? 1 : totalDuration
      return {
        primaryMetricValue: data.totalDuration,
        primaryMetricIntegerPercent: Math.round(
          (data.totalDuration / total) * 100,
        ),
        primaryMetric: formatDuration(data.totalDuration),
        secondaryMetric: formatSessions(data.sessionCount),
        tertiaryMetric: formatAds(data.adCount),
      }
    }
    case 'ads': {
      const total = totalAdCount == 0 ? 1 : totalAdCount
      return {
        primaryMetricValue: data.adCount,
        primaryMetricIntegerPercent: Math.round((data.adCount / total) * 100),
        primaryMetric: formatAds(data.adCount),
        secondaryMetric: formatDuration(data.totalDuration),
        tertiaryMetric: formatSessions(data.sessionCount),
      }
    }
    case 'sessions': {
      const total = totalSessionCount == 0 ? 1 : totalSessionCount
      return {
        primaryMetricValue: data.sessionCount,
        primaryMetricIntegerPercent: Math.round(
          (data.sessionCount / total) * 100,
        ),
        primaryMetric: formatSessions(data.sessionCount),
        secondaryMetric: formatDuration(data.totalDuration),
        tertiaryMetric: formatAds(data.adCount),
      }
    }
  }
}
