import API from './CoreAPI'
import Options from './Options'
import OnboardingOverlayController from '~/components/OnboardingOverlay/OnboardingOverlayController'
import $ from 'jquery'

const onboardingController = new OnboardingOverlayController()

$(document).ready(function () {
  Options.init(async function () {
    $('#selectAllActiveDays').click(function () {
      $('.activeDay').prop('checked', true)

      Options.setActiveDays()
    })

    const installId = await API.Storage.get('install-id')
    const installIdEl = document.getElementById('installIdDisplay')
    if (installIdEl) installIdEl.textContent = installId

    API.Analytics.bindHtmlEvents()

    onboardingController.init()
  })
})
