import API from '~/core/CoreAPI'
import { DAY_IN_SEC } from '~/core/Time'

/**
 * Is can setting max time allowed to groups or to all websites
 * @returns {boolean}
 */
export function isSetMaxTimeAllowed(
  allowedTime: number,
  elapsedTime: number,
  newAllowedTime: number,
  isAll: boolean = false,
) {
  const totalSecondsRemaining = allowedTime - elapsedTime

  let allow = true

  if (newAllowedTime == 0 && isAll) {
    // Don't allow setting time to 0 minutes for all websites
    alert(API.Chrome.Translation.get('cannotSetTimeToZero'))
    return false
  }

  if (newAllowedTime >= DAY_IN_SEC) {
    alert(API.Chrome.Translation.get('cannotSetMoreThan1440Mins'))
    return false
  }

  if (newAllowedTime < 0) {
    alert(API.Chrome.Translation.get('cannotSetTimeToZeroOrLess'))
    return false
  }

  if (elapsedTime >= newAllowedTime) {
    allow = confirm(API.Chrome.Translation.get('allSitesBlockedImmediately'))
    if (!allow) return false
  }

  if (newAllowedTime > allowedTime) {
    if (API.Settings.get('productivityBypass') === true) {
      alert(API.Chrome.Translation.get('completeChallengeBeforeIncreasingTime'))
      return false
    }

    if (totalSecondsRemaining < 180) {
      allow = confirm(API.Chrome.Translation.get('lessThanThreeMins'))

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('lessThanThreeMins2'))
      }

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('lessThanThreeMins3'))
      }

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('lessThanThreeMins4'))
      }

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('lessThanThreeMins5'))
      }

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('lessThanThreeMins6'))
      }

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('lessThanThreeMins7'))
      }

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('lessThanThreeMins8'))
      }

      if (allow === true) {
        alert(API.Chrome.Translation.get('tellingYourMom'))

        window.open('https://asc.calpoly.edu/ssl/procrastination')
      }
    } else {
      allow = confirm(API.Chrome.Translation.get('maybeYouShouldReconsider'))

      if (allow === true) {
        allow = confirm(API.Chrome.Translation.get('onlyHurtingYourself'))
      }

      if (allow === true) {
        alert(API.Chrome.Translation.get('meow'))
      }
    }
  }

  if (allow === true) {
    let updatedMsg = API.Chrome.Translation.get('settingsUpdated')

    if (newAllowedTime < allowedTime) {
      updatedMsg =
        API.Chrome.Translation.get('givingLessTime') + '\n\n' + updatedMsg
    }

    alert(updatedMsg)
  }

  return allow
}
