<script lang="ts" setup>
import {
  ArcElement,
  CategoryScale,
  Chart,
  ChartData,
  ChartOptions,
  Filler,
  Legend,
  LinearScale,
  PieController,
  DoughnutController,
  PointElement,
  Title,
  Tooltip,
  TooltipOptions,
  Plugin,
} from 'chart.js'
import { Doughnut } from 'vue-chartjs'
import { computed, ref } from 'vue'
import { useChartColors } from '../composables/useChartColors'

export interface ChartSeries {
  /**
   * The name of the series.
   */
  name?: string
  /**
   * The Y-axis values for each label at the same index.
   */
  values: number[]
}

export interface PieChartData {
  /**
   * The labels shown on the X-axis.
   */
  labels: string[]
  series: ChartSeries[]
}

const props = defineProps<{
  title?: string
  data: PieChartData
  mainColor?: string
  yAxis?: {
    formatTickDisplayValue?(value: string | number): string
    minStepSize?: number
    suggestedMax?: number
  }
  tooltip?: {
    getLabel: DoughnutTooltipOptions['callbacks']['label']
  }
}>()

// Register chart components we use to tree-shake the rest out
Chart.register(
  PieController,
  DoughnutController,
  CategoryScale,
  LinearScale,
  PointElement,
  ArcElement,
  Filler,
  Title,
  Tooltip,
)

type DoughnutChartData = ChartData<'doughnut', number[]>
type DoughnutOptions = ChartOptions<'doughnut'>
type DoughnutTooltipOptions = TooltipOptions<'doughnut'>

const chart = ref<HTMLElement>()
const plugins = [Filler, Legend] as Plugin<'doughnut'>[]
const { textColor, titleColor, getSeriesColor } = useChartColors()

const fillColors = computed(() => {
  if (props.data.series.length === 0) {
    return [getSeriesColor(0).fillColor]
  }
  return props.data.series[0].values.map(
    (series, i) => getSeriesColor(i).fillColor,
  )
})

const borderColors = computed(() => {
  if (props.data.series.length === 0) {
    return [getSeriesColor(0).borderColor]
  }
  return props.data.series[0].values.map(
    (series, i) => getSeriesColor(i).borderColor,
  )
})

const chartData = computed<DoughnutChartData>(() => ({
  labels: props.data.labels,
  datasets: props.data.series.map((series) => {
    return {
      label: series.name ? ' ' + series.name + '   ' : undefined,
      data: series.values,
      tension: 0.2,
      borderColor: borderColors.value,
      borderWidth: 2,
      pointBackgroundColor: borderColors.value,
      pointHoverBackgroundColor: borderColors.value,
      pointHoverBorderWidth: 0,
      pointRadius: 3,
      pointHoverRadius: 6,
      hoverOffset: 4,
      backgroundColor: fillColors.value,
    }
  }),
}))

const chartOptions = computed<DoughnutOptions>(() => ({
  responsive: true,
  maintainAspectRatio: false,
  animation: {
    duration: 500,
  },
  interaction: {
    intersect: false,
    mode: 'nearest',
    axis: 'x',
  },
  plugins: {
    title: {
      display: !!props.title,
      text: props.title,
      color: titleColor.value,
    },
    tooltip: {
      callbacks: {
        label: props.tooltip?.getLabel,
      },
    },
    legend: {
      // Show the legend if there are any named series
      display: true, //props.data.series.filter(s => !!s.name).length !== 0,
      position: 'bottom',
      labels: {
        color: textColor.value,
        usePointStyle: true,
        pointStyle: 'circle',
        boxWidth: 8,
        boxHeight: 8,
      },
    },
  },
}))
</script>

<template>
  <!-- A simple wrapper around the Chart.js pie chart with no business logic -->
  <doughnut
    ref="chart"
    :data="chartData"
    :options="chartOptions"
    :plugins="plugins"
  />
</template>
