<script lang="ts" setup>
defineProps<{
  groupName: string
}>()

const emits = defineEmits<{
  (event: 'confirm', value: boolean): void
}>()

function confirm() {
  emits('confirm', true)
}

function cancel() {
  emits('confirm', false)
}
</script>

<script lang="ts">
export default {
  name: 'DeleteGroupConfirm',
}
</script>

<template>
  <div class="p-4">
    <h3 class="font-bold text-lg">Delete blocked group</h3>
    <p>
      Are you sure you want to delete a blocked group
      <span class="font-bold">{{ groupName }}</span> ?
    </p>
  </div>
  <div class="px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
    <button
      @click="confirm"
      class="btn btn-outline btn-error btn-sm not-prose ml-3"
    >
      DELETE GROUP
    </button>
    <button
      type="button"
      class="btn btn-sm"
      @click="cancel"
    >
      CANCEL
    </button>
  </div>
</template>
