import { API } from './API'
import GeneralUtils from './GeneralUtils'

export interface ComponentDefinition {
  view: () => Promise<Record<string, any>>
  controller: () => Promise<Record<string, any>>
  model: () => Promise<Record<string, any>>
}

export interface Component {
  view: Record<string, any>
  controller: Record<string, any>
  model: Record<string, any>
}

interface ComponentDefinitions {
  OnboardingOverlay: ComponentDefinition
  ChallengeOptions: ComponentDefinition
  BlockedPageOnboardingRequiredOverlay: ComponentDefinition
  ActivityMonitorOptions: ComponentDefinition
  ChallengeOverlay: ComponentDefinition
  ActivityMonitorOverlay: ComponentDefinition
  Challenge: ComponentDefinition
}

type ComponentName = keyof ComponentDefinitions
interface IComponentOptions {
  name: ComponentName
  instance?: string
  view?: string
  onLoaded?: Function
}

const log = GeneralUtils.createScopedLog('ComponentFactory')

export class ComponentFactory {
  _api: API
  _components: Record<string, any> = {}
  _instances: Record<string, any> = {}

  _definitions = {
    Challenge: {
      model: import('~/components/Challenge/ChallengeModel'),
      view: import('~/components/Challenge/ChallengeView'),
      controller: import('~/components/Challenge/ChallengeController'),
    },
    ChallengeOptions: {
      model: import('~/components/Challenge/ChallengeModel'),
      view: import('~/components/Challenge/views/options/ChallengeOptionsView'),
      controller: import('~/components/Challenge/ChallengeController'),
    },
    ChallengeOverlay: {
      model: import('~/components/Challenge/ChallengeModel'),
      view: import('~/components/Challenge/views/overlay/ChallengeOverlayView'),
      controller: import('~/components/Challenge/ChallengeController'),
    },
    ActivityMonitorOptions: {
      model: import('~/components/ActivityMonitor/ActivityMonitorModel'),
      view: import(
        '~/components/ActivityMonitor/views/options/ActivityMonitorOptionsView'
      ),
      controller: import(
        '~/components/ActivityMonitor/ActivityMonitorController'
      ),
    },
    ActivityMonitorOverlay: {
      model: import('~/components/ActivityMonitor/ActivityMonitorModel'),
      view: import(
        '~/components/ActivityMonitor/views/overlay/ActivityMonitorOverlayView'
      ),
      controller: import(
        '~/components/ActivityMonitor/ActivityMonitorController'
      ),
    },
    OnboardingOverlay: {
      model: import('~/components/OnboardingOverlay/OnboardingOverlayModel'),
      view: import('~/components/OnboardingOverlay/OnboardingOverlayView'),
      controller: import(
        '~/components/OnboardingOverlay/OnboardingOverlayController'
      ),
    },
    BlockedPageOnboardingRequiredOverlay: {
      model: import(
        '~/components/BlockedPageOnboardingRequiredOverlay/BlockedPageOnboardingRequiredOverlayModel'
      ),
      view: import(
        '~/components/BlockedPageOnboardingRequiredOverlay/BlockedPageOnboardingRequiredOverlayView'
      ),
      controller: import(
        '~/components/BlockedPageOnboardingRequiredOverlay/BlockedPageOnboardingRequiredOverlayController'
      ),
    },
  }

  constructor(API: API) {
    this._api = API
  }

  load(options: IComponentOptions) {
    const componentName = options.name
    const instanceName = options.instance || this.createInstanceID(options.name)
    const onLoaded =
      typeof options.onLoaded === 'function' ? options.onLoaded : null

    log(
      'load',
      'Loading ' +
        instanceName +
        ' instance of ' +
        componentName +
        ' component',
      null,
      'DEBUG',
    )

    /* Try to get the component if it has already been loaded and instantiated */
    try {
      const component = this.get(componentName, instanceName)

      if (typeof onLoaded === 'function') {
        onLoaded(component)
      }
    } catch (e) {
      /* If the component has already been loaded, just instantiate it */
      if (this._components[componentName]) {
        const instance = this.instantiate(
          this._components[componentName],
          instanceName,
        )

        if (typeof onLoaded === 'function') {
          onLoaded(instance)
        }
      } else {
        /* If the component hasn't been loaded yet, load and instantiate it */
        this.require(componentName, instanceName, onLoaded)
      }
    }
  }

  loadAsync(options: IComponentOptions): Promise<any> {
    return new Promise((res) => {
      this.load({ ...options, onLoaded: res })
    })
  }

  /**
   * Loads component files, storing classes in local variable then instantiating each
   *
   * @param componentName The name of the component to import and load. Can be any of the keys
   *                      from `API.Component.definitions`
   * @param instanceName Key used to identify component instance
   * @param view Component view to load
   * @param onLoaded Function to call once component is loaded and instantiated
   */
  require(
    componentName: ComponentName,
    instanceName: string,
    onLoaded: Function | null,
  ) {
    log(
      'require',
      'Requiring ' +
        instanceName +
        ' instance of ' +
        componentName +
        ' component',
      null,
      'DEBUG',
    )

    if (this._definitions[componentName] === null) {
      throw Error(
        `Component named ${componentName} not found. Options ${Object.keys(
          this._definitions,
        )}`,
      )
    }
    const { model, view, controller } = this._definitions[componentName]

    /* Load Component and MVC files */
    Promise.all([import('~/core/Component'), model, view, controller]).then(
      async ([Component, Model, View, Controller]) => {
        log(
          'require',
          `${instanceName} instance of ${componentName} component require complete`,
          null,
          'DEBUG',
        )

        /* Store classes in local variable for future re-use */
        this._components[componentName] = new Component.default(componentName, {
          Model: Model.default,
          View: View.default,
          Controller: Controller.default,
        })

        const instance = this.instantiate(
          this._components[componentName],
          instanceName,
        )

        if (typeof onLoaded === 'function') {
          onLoaded(instance)
        }
      },
    )
  }

  /**
   * Instantiates component classes and stores them in local variable
   *
   * @param component The component to instantiate
   * @param instanceName Key used to identify component instance
   * @returns {*}
   */
  instantiate(
    component: { name: ComponentName; create: Function },
    instanceName: string,
  ) {
    const componentName = component.name

    log(
      'instantiate',
      'Instantiating ' +
        instanceName +
        ' instance of ' +
        componentName +
        ' component',
      null,
      'DEBUG',
    )

    /* Create new instances */
    const controller = component.create('Controller', instanceName)
    const model = component.create('Model', instanceName)
    const view = component.create('View', instanceName)
    let collection = null
    const hasCollection = typeof controller.collection !== 'undefined'

    /* If the component has a collection, instantiate it and add it to the instances */
    if (hasCollection) {
      collection = component.create('Collection', instanceName)
      controller.collection = collection
      view.collection = collection
    }

    controller.model = model
    controller.view = view
    view.model = model

    /* If the controller has an initAPI() method, call it so controller can
     * expose its methods to the API */
    if (typeof controller.initAPI === 'function') {
      controller.initAPI(API)
    }

    this._instances[componentName] =
      typeof this._instances[componentName] === 'object' &&
      this._instances[componentName] !== null
        ? this._instances[componentName]
        : {}

    /* Store instances in local variable for future re-use */
    this._instances[componentName][instanceName] = {
      controller: controller,
      model: model,
      view: view,
      collection: collection,
    }

    return this._instances[componentName][instanceName]
  }

  /**
   * Gets component instance
   *
   * @param componentName The name of the component to get
   * @param instanceName Key used to identify component instance
   */
  get(componentName: ComponentName, instanceName: string) {
    log(
      'get',
      'Getting ' +
        instanceName +
        ' instance of ' +
        componentName +
        ' component',
      null,
      'TRACE',
    )

    if (!componentName) {
      throw new Error('[ComponentFactory.get()] Must specify component name')
    } else if (!(componentName in this._instances)) {
      throw new Error(
        '[ComponentFactory.get()] ' +
          componentName +
          ' component has not been loaded yet',
      )
    } else if (!(instanceName in this._instances[componentName])) {
      throw new Error(
        '[ComponentFactory.get()] ' +
          instanceName +
          ' instance of ' +
          componentName +
          ' component has not been loaded yet',
      )
    }

    return this._instances[componentName][instanceName]
  }

  /**
   * Gets all instances of component
   *
   * @param componentName The name of the component to get
   * @returns {*}
   */
  getAll(componentName: ComponentName) {
    log(
      'get',
      'Getting all instances of ' + componentName + ' component',
      null,
      'TRACE',
    )

    if (!componentName) {
      throw new Error(
        '[ComponentFactory.getAll()] Must specify component componentName',
      )
    } else if (!(componentName in this._instances)) {
      throw new Error(
        '[ComponentFactory.getAll()] ' +
          componentName +
          ' component has not been loaded yet',
      )
    }

    return this._instances[componentName]
  }

  /**
   * Gets uninstantiated class of component
   *
   * @param componentName The component to get the class from
   * @param className The class to get
   * @returns {*}
   */
  getClass(componentName: ComponentName, className: string) {
    if (!componentName) {
      throw new Error('[ComponentFactory.get()] Must specify component name')
    } else if (!(componentName in this._components)) {
      throw new Error(
        '[ComponentFactory.get()] ' +
          componentName +
          ' component has not been loaded yet',
      )
    }

    return this._components[componentName][className]
  }

  /**
   * Gets uninstantiated classes of component
   *
   * @param componentName The component to get classes from
   * @returns {*}
   */
  getClasses(componentName: ComponentName) {
    if (!componentName) {
      throw new Error('[ComponentFactory.get()] Must specify component name')
    } else if (!(componentName in this._components)) {
      throw new Error(
        '[ComponentFactory.get()] ' +
          componentName +
          ' component has not been loaded yet',
      )
    }

    return this._components[componentName]
  }

  /**
   * Creates instance ID
   *
   * @param componentName The name of the component
   * @returns {string}
   */
  createInstanceID(componentName: ComponentName) {
    return API.Utils.firstToLower(componentName) + '-' + new Date().getTime()
  }
}
