<script lang="ts" setup>
import { ref, computed, watch, onMounted } from 'vue'
import { API } from '~/core/API'
import TimeDonutChart from '~/components/BlockedGroups/TimeDonutChart.vue'
import Time from '~/core/Time'
import { BlockTime } from '~/core/BlockedAllWebsites'
import { isSetMaxTimeAllowed } from '~/utils/isSetMaxTimeAllowed'

const emits = defineEmits<{
  (event: 'blockAllWebsites', value: boolean): void
}>()

const defaultTimeValue: BlockTime = {
  allowedTime: 0,
  elapsedTime: 0,
}

const blockAllSites = ref<boolean>()

const time = ref<BlockTime>(defaultTimeValue)

async function setBlockAllSites() {
  await API.Settings.setAsync({ isBlockAllWebsites: !blockAllSites.value })
}

const allowedTime = computed(() => {
  return Time.getTimeFromSeconds(Math.round(time.value.allowedTime))
})
const remainingTime = computed(() => {
  return Time.getTimeFromSeconds(
    Math.round(time.value.allowedTime - time.value.elapsedTime),
  )
})

const timeExpired = computed(
  () =>
    remainingTime.value.hours == 0 &&
    remainingTime.value.minutes == 0 &&
    remainingTime.value.seconds == 0,
)

const maxTimeAllowedInHours = ref<number>(0)
const maxTimeAllowedInMinutes = ref<number>(0)
const challengeRequired = ref<boolean>()

API.PubSub.listen('Challenge.complete', () => {
  challengeRequired.value = false
})

onMounted(() => {
  challengeRequired.value = API.Settings.get('challengeRequired') ?? false

  blockAllSites.value = API.Settings.get('isBlockAllWebsites') ?? false
  if (blockAllSites.value) {
    time.value = getTime()
    fillMaxTimeAllowed()
  }
})

const canChangeTime = computed(
  () => !(timeExpired.value || challengeRequired.value),
)

watch(blockAllSites, (value) => {
  if (value) {
    time.value = getTime()
    fillMaxTimeAllowed()
  }
  emits('blockAllWebsites', Boolean(value))
})

function validateHours(e: any) {
  if (e.target.value > 23) maxTimeAllowedInHours.value = 23
}

function validateMinutes(e: any) {
  if (e.target.value > 59) maxTimeAllowedInMinutes.value = 59
}

function getTime(): BlockTime {
  if (blockAllSites.value) {
    let valueFromStorage = API.BlockedAllWebsites.getTime()
    if (valueFromStorage == null) return defaultTimeValue
    else return valueFromStorage
  } else return defaultTimeValue
}

function fillMaxTimeAllowed() {
  maxTimeAllowedInHours.value = allowedTime.value.hours
  maxTimeAllowedInMinutes.value = allowedTime.value.minutes
}

async function saveChanges() {
  const newMaxAllowedTime = Time.getTimeInSeconds(
    maxTimeAllowedInHours.value,
    maxTimeAllowedInMinutes.value,
  )

  if (
    time.value.allowedTime != newMaxAllowedTime &&
    !isSetMaxTimeAllowed(
      time.value.allowedTime,
      time.value.elapsedTime,
      newMaxAllowedTime,
      true,
    )
  ) {
    maxTimeAllowedInHours.value = allowedTime.value.hours
    maxTimeAllowedInMinutes.value = allowedTime.value.minutes
  } else {
    await API.Settings.setAsync({
      blockAllWebsitesAllowedTime: newMaxAllowedTime,
    })
    const timeObj = Time.getTimeFromSeconds(newMaxAllowedTime)
    const timeInMinutes = timeObj.hours * 60 + timeObj.minutes
    API.Analytics.event('CLICK_SET_MAX_ALLOWED_TIME', {
      minutes: timeInMinutes,
    })

    time.value = getTime()
  }
}
</script>

<script lang="ts">
export default {
  name: 'BlockAllWebsites',
}
</script>

<template>
  <label
    class="cursor-pointer label justify-start mt-2"
    id="blockAllSitesForGuide"
  >
    <input
      type="checkbox"
      @click="setBlockAllSites"
      v-model="blockAllSites"
      :disabled="challengeRequired"
      data-testid="blockAllSites"
      class="checkbox checkbox-sm checkbox-primary mr-2"
    />
    <span class="label-text"
      >Block all websites except the ones in Allowed Sites list</span
    ></label
  >
  <div
    class="flex flex-row p-3"
    v-if="blockAllSites"
  >
    <time-donut-chart
      :allowed-time="time.allowedTime"
      :elapsed-time="time.elapsedTime"
      data-testid="maxTimeAllowedInHoursForAllView"
    />
    <div class="pl-3 grow">
      <p class="inline-block whitespace-nowrap">Max Time Allowed per Day</p>
      <div class="join">
        <label class="input-group ml-3">
          <input
            type="number"
            min="0"
            v-model="maxTimeAllowedInHours"
            :disabled="!canChangeTime"
            @input="validateHours"
            data-testid="maxTimeAllowedInHoursForAllSites"
            class="input input-bordered input-sm join-item w-28"
          />
          <span class="rounded-none">hr</span>
          <input
            type="number"
            min="0"
            max="59"
            :disabled="!canChangeTime"
            @input="validateMinutes"
            v-model="maxTimeAllowedInMinutes"
            data-testid="maxTimeAllowedInMinutesForAllSites"
            class="input input-bordered input-sm join-item w-28"
          />
          <span class="rounded-none">min</span>
        </label>
      </div>
      <p
        v-if="timeExpired"
        class="alert alert-error"
      >
        {{ $t('timeExpiredCannotChangeUntilTomorrow') }}
      </p>
      <button
        v-if="canChangeTime"
        class="btn btn-primary btn-sm mt-3"
        data-testid="saveTimeForAllWebsitesBtn"
        @click="saveChanges()"
      >
        SAVE CHANGES
      </button>
    </div>
  </div>
</template>
