<script lang="ts" setup>
import { API } from '~/core/API'
import { ref } from 'vue'

const websitesForAdding = ref<string>()
const whitelist = ref<string[]>()
const isNuclearOptionEnabled = ref<boolean>(false)

API.Settings.init(async () => {
  await API.WhitelistRepository.init()
  getWebsites()

  isNuclearOptionEnabled.value = getNuclearOption()
})

async function addWebsites() {
  if (websitesForAdding.value) {
    const newDomainsArray = websitesForAdding.value.split('\n')
    await API.WhitelistRepository.add(newDomainsArray)
    getWebsites()
    websitesForAdding.value = undefined
  }
}

async function deleteWebsite(website: string) {
  whitelist.value = whitelist.value?.filter((x) => x != website)
  await API.WhitelistRepository.delete(website)
}

function getWebsites() {
  whitelist.value = API.WhitelistRepository.getAll()
}

function getNuclearOption() {
  return (
    API.NuclearOption.isActive() &&
    API.NuclearOption.getBlockType() === 'blocked'
  )
}
</script>

<template>
  <div>
    <div class="prose flex flex-col">
      <h1>{{ $t('allowedWebsites') }}</h1>
    </div>
    <div class="mt-0">
      Add site(s) to allow in the text box below, then click "Add Allowed
      Site(s)". Some tips:
      <ul class="tips text-sm">
        <li>To allow multiple sites, add one per line.</li>
        <li>Do not type the http:// or https://</li>
        <li>
          If you want to allow the entire site, leave off the www (i.e. cnn.com
          instead of www.cnn.com)
        </li>
        <li>
          You can use a preceding asterisk as a wildcard (i.e. *.com allows all
          .com sites, or *foo allows all domains with the word "foo" in them)
        </li>
      </ul>
    </div>
    <textarea
      class="textarea textarea-bordered w-full"
      :disabled="isNuclearOptionEnabled"
      placeholder="Enter sites here..."
      v-model="websitesForAdding"
    />
    <input
      type="button"
      value="Add Allowed Site(s)"
      class="btn btn-sm btn-primary mt-2"
      :disabled="isNuclearOptionEnabled"
      @click="addWebsites"
    />
    <p
      v-if="isNuclearOptionEnabled"
      class="alert alert-error"
    >
      {{
        API.Chrome.Translation.get('cannotAddAllowedSitesDuringNuclearOption')
      }}
    </p>
    <p v-else>
      Click the
      <iconify-icon
        icon="mdi:close"
        class="text-error align-middle"
      />
      to remove a site from the list.
    </p>
    <ul
      class="w-full border border-neutral-400 list-none px-0 not-prose"
      id="allowedSitesList"
    >
      <p
        v-if="whitelist?.length == 0"
        class="m-auto p-2"
      >
        {{ $t('noSitesYet') }}
      </p>
      <li
        class="mt-1 mb-1"
        v-for="website in whitelist"
        :key="website"
      >
        <button
          v-if="!isNuclearOptionEnabled"
          class="btn btn-sm btn-ghost btn-circle mr-2"
          @click="deleteWebsite(website)"
        >
          <iconify-icon
            icon="mdi:close"
            class="cursor-pointer align-middle text-error"
            data-url="website"
          />
        </button>
        <p class="inline-block ml-1">{{ website }}</p>
      </li>
    </ul>
    <p>
      <small class="disclaimer"
        >You can remove sites from the Allowed Sites list at any time,
        regardless of whether your time has expired.</small
      >
    </p>
  </div>
</template>
