<script lang="ts" setup>
import { computed, ref } from 'vue'
import { useUsageHighlights } from '~/composables/useUsageHighlights'
import Time, { SECOND, MONTH } from '~/core/Time'
import DateUtils from '~/core/DateUtils'

const highlightsOptions = ref({
  date: Time.getEndOfToday(),
  interval: MONTH,
})

const { data: highlightsData } = useUsageHighlights(highlightsOptions)

const formattedFavoriteHour = computed(() => {
  const startHour = highlightsData.value?.favoriteHour ?? 0
  const endHour = startHour === 23 ? 0 : startHour + 1

  const start = DateUtils.parseMilitaryTime(`${startHour}:00`).display
  const end = DateUtils.parseMilitaryTime(`${endHour}:00`).display
  return `${start} – ${end}`
})

const formattedTotalDuration = computed(() => {
  return Time.formatDurationInSeconds(
    (highlightsData.value?.totalDuration ?? 0) / SECOND,
  )
})
</script>

<template>
  <details
    class="collapse collapse-arrow border rounded-md border-slate-300 bg-base-200/50 select-none"
  >
    <summary class="collapse-title text-base font-medium">
      {{ $t('dashboardHighlights') }}
    </summary>

    <div
      class="collapse-content"
      v-if="highlightsData"
    >
      <ul class="list-disc">
        <li>
          {{
            $t('dashboardHighlightsCounts', [
              `${highlightsData.appCount}`,
              `${highlightsData.sessionCount}`,
            ])
          }}
        </li>
        <li>
          {{ $t('dashboardHighlightsTime', formattedTotalDuration) }}
        </li>
        <li>
          {{ $t('dashboardHighlightsFavoriteTime', formattedFavoriteHour) }}
        </li>
        <li>
          {{
            $t('dashboardHighlightsFavoriteDay', [
              `${highlightsData.blockedAppCount}`,
              `${highlightsData.blockedSessionCount}`,
            ])
          }}
        </li>
        <li>
          {{ $t('dashboardHighlightsAds', `${highlightsData.adCount}`) }}
        </li>
      </ul>
    </div>
  </details>
</template>
