<script lang="ts" setup>
import InPlaceDialog from './InPlaceDialog.vue'
import SfDateRangeInput from './SfDateRangeInput.vue'
import { useVModel } from '@vueuse/core'
import { ChartSource, WebsiteType } from '~/composables/useUsageSummary'
import { ref } from 'vue'
import { DateInterval } from '../composables/useDateRange'

const props = defineProps<{
  activeChartSource: ChartSource
  activeWebsiteType: WebsiteType
  range: DateInterval
  searchText: string
  formattedStartDate: string
  formattedEndDate: string
}>()

const emit = defineEmits<{
  (event: 'update:activeChartSource', newValue: ChartSource): void
  (event: 'update:activeWebsiteType', newValue: WebsiteType): void
  (event: 'update:searchText', newValue: string): void
  (event: 'update:range', newValue: DateInterval): void
}>()

const activeChartSource = useVModel(props, 'activeChartSource', emit)
const activeWebsiteType = useVModel(props, 'activeWebsiteType', emit)
const searchText = useVModel(props, 'searchText', emit)
const range = useVModel(props, 'range', emit)

const searchExpanded = ref(false)
const isShowingDatePicker = ref(false)
const searchRef = ref<HTMLInputElement>()

function toggleDatePicker() {
  isShowingDatePicker.value = !isShowingDatePicker.value
}
function hideDatePicker() {
  isShowingDatePicker.value = false
}

async function toggleSearch() {
  searchExpanded.value = !searchExpanded.value
  if (searchExpanded.value) {
    await new Promise((r) => setTimeout(r, 100))
    searchRef.value?.focus()
  }
}

function onDateSelected(startDate: number, endDate: number) {
  range.value = { startDate, endDate }
  hideDatePicker()
}
</script>

<template>
  <div class="flex justify-between items-end">
    <div class="join lg:join-horizontal max-lg:join-vertical">
      <div class="flex flex-col-reverse">
        <select
          id="chart-source"
          class="join-item select select-primary select-bordered min-w-0"
          v-model="activeChartSource"
        >
          <option value="duration">
            {{ $t('visitDuration') }}
          </option>
          <option value="sessions">
            {{ $t('sessions') }}
          </option>
          <option value="ads">
            {{ $t('ads') }}
          </option>
        </select>
        <label
          for="chart-source"
          class="hidden lg:block mb-0.5 text-base-content/80"
          ><small>{{ $t('showLabel') }}</small></label
        >
      </div>

      <div class="flex flex-col">
        <label
          for="website-type"
          class="hidden lg:block mb-0.5 text-base-content/80"
          ><small>{{ $t('filterLabel') }}</small></label
        >
        <select
          id="website-type"
          class="join-item select select-primary select-bordered min-w-0"
          v-model="activeWebsiteType"
        >
          <option value="all">
            {{ $t('allWebsites') }}
          </option>
          <option value="allowed">
            {{ $t('allowedWebsites') }}
          </option>
          <option value="blocked">
            {{ $t('blockedWebsites') }}
          </option>
        </select>
      </div>
    </div>

    <div class="join join-vertical lg:join-horizontal">
      <button
        class="join-item btn btn-primary btn-outline border flex items-center space-x-1 pl-4 pr-1.5 py-1.5 text-lg font-medium text-primary select-none cursor-pointer bg-primary bg-opacity-0 hover:bg-opacity-hover active:bg-opacity-active transition-all"
        :title="$t('selectDateRange')"
        @click="toggleDatePicker"
      >
        <span class="text-xs"
          >{{ formattedStartDate }} <br />
          {{ formattedEndDate }}</span
        >

        <iconify-icon
          icon="mdi:calendar-range"
          width="20"
        />
      </button>

      <in-place-dialog
        :visible="isShowingDatePicker"
        class="float-right"
        @dismiss="hideDatePicker"
      >
        <div class="flex flex-col p-2 bg-base-100">
          <sf-date-range-input
            :start-date="range.startDate"
            :end-date="range.endDate"
            @date-selected="onDateSelected"
          />
        </div>
      </in-place-dialog>

      <input
        ref="searchRef"
        data-testid="search-input"
        v-model.trim="searchText"
        class="join-item input input-primary input-outline input-bordered transition-all ease-in-out"
        :class="{ expanded: searchExpanded, collapsed: !searchExpanded }"
        type="text"
        :placeholder="$t('search')"
      />

      <button
        @click="toggleSearch()"
        class="join-item btn btn-primary border hidden lg:flex"
        :class="{
          'btn-outline': !searchExpanded,
        }"
      >
        <div class="indicator">
          <span
            class="indicator-item badge badge-xs badge-error hidden lg:flex"
            v-if="searchText != '' && !searchExpanded"
          />
          <iconify-icon
            icon="mdi:search"
            width="20"
          />
        </div>
      </button>
    </div>
  </div>
</template>

<style scoped>
.collapsed {
  @apply lg:w-0;
  @apply lg:invisible;
  @apply lg:!ml-0;
  @apply lg:border-0;
  @apply lg:p-0;
  @apply lg:w-0;
}

.expanded {
  @apply w-48;
}
</style>
