<script lang="ts" setup>
import { useEventListener } from '@vueuse/core'

defineProps<{
  visible: boolean
  teleportDisabled?: boolean
  blurred?: boolean
}>()

const emit = defineEmits<{
  shown: []
  afterShowAnimation: []
  // Fired after the dialog is not longer visible
  dismissed: []
  // Fired when the dialog should be dismissed
  dismiss: []
}>()

const DIALOG_TRANSITION_DURATION = 500

function dismiss() {
  emit('dismiss')
}

useEventListener('keydown', (event) => {
  if (event.key === 'Escape') dismiss()
})
</script>

<script lang="ts">
export default {
  name: 'BaseModal',
}
</script>

<template>
  <teleport
    to="body"
    :disabled="teleportDisabled"
  >
    <transition
      name="dialog-fade-up"
      :duration="DIALOG_TRANSITION_DURATION"
    >
      <div
        v-if="visible"
        class="fixed inset-0 z-[1003] flex flex-col items-center justify-center bg-black bg-opacity-50 cursor-pointer"
        :class="{ ' backdrop-blur-lg': blurred }"
        @mousedown="dismiss"
      >
        <div
          class="card min-w-[20rem] max-h-[95vh] max-w-[90vw] bg-surface bg-white rounded-lg cursor-default overflow-y-auto"
        >
          <slot />
        </div>
      </div>
    </transition>
  </teleport>
</template>

<style scoped>
/* Background Transition */
.dialog-fade-up-leave-active,
.dialog-fade-up-enter-active {
  @apply transition-opacity;
}

.dialog-fade-up-enter-from,
.dialog-fade-up-leave-to {
  @apply opacity-0;
}

/* Card Transition */
.dialog-fade-up-leave-active .card,
.dialog-fade-up-enter-active .card {
  @apply transition-transform;
}

.dialog-fade-up-enter-from .card,
.dialog-fade-up-leave-to .card {
  @apply transform translate-y-16;
}
</style>
