import API from '~/core/CoreAPI'
import Browser from 'webextension-polyfill'

var log = API.Utils.createScopedLog('OnboardingOverlayView')

export default class OnboardingOverlayView {
  constructor() {
    log('constructor', 'Constructing OnboardingOverlayView', null, 'DEBUG')
  }

  init(referrer) {
    log('init', 'Initializing view', null, 'DEBUG')
    // Create the URL
    const isExistingUser = !API.Settings.get('isNewUser')
    const url = new URL(
      Browser.runtime.getURL('/components/Onboarding/onboarding.html'),
    )
    url.searchParams.set('referrer', referrer)
    url.searchParams.set('isExistingUser', isExistingUser)

    // Create the iframe
    const iframe = document.createElement('iframe')
    iframe.src = url
    iframe.style.width = '100%'
    iframe.style.height = '100%'
    iframe.style.left = 0
    iframe.style.top = 0
    iframe.style.margin = 0
    iframe.style.padding = 0
    iframe.style.position = 'fixed'
    iframe.style.backgroundColor = 'transparent'
    iframe.style.zIndex = 2000
    iframe.setAttribute('allowTransparency', 'true')
    iframe.setAttribute('frameBorder', '0')

    // Mount the iframe
    document.body.style.overflow = 'hidden'
    document.body.append(iframe)

    // Add dismiss listener `window.parent.postMessage('OnboardingOverlay.dismiss', '*')`
    window.addEventListener('message', (event) => {
      if (event.data !== 'OnboardingOverlay.dismiss') return

      iframe.remove()
      document.body.style.overflow = 'auto'
    })
  }
}
