import { API } from '../core/API'
import {
  getAllowlist,
  getBlocklist,
  isHostnameBlocked,
} from './isHostnameBlocked'

export interface UsageSummary {
  appId: string
  sessionCount: number
  totalDuration: number
  blocked: boolean
  allowed: boolean
  adCount: number
}

export interface UsageHighlights {
  totalDuration: number
  appCount: number
  sessionCount: number
  blockedSessionCount: any
  blockedAppCount: any
  favoriteHour: number
  adCount: number
}

let api: API
export class UsageDatabaseClient {
  constructor(API: API) {
    api = API
  }

  async getUsageSummary(
    startDate: number,
    endDate: number,
  ): Promise<UsageSummary[]> {
    const usageDatabase = await api.WebUsage.getUsageDatabase()
    const sessions = await usageDatabase.sessionsRepo.findSessions(
      startDate,
      endDate,
    )
    const ads = await usageDatabase.adsRepo.findAds(startDate, endDate)

    // const now = Date.now()
    const blocklist = getBlocklist()
    const allowlist = await getAllowlist()

    const aggregatedSessionsByAppId = sessions.reduce(
      (
        prev: {
          [key: string]: UsageSummary
        },
        session,
      ) => {
        const appId = session.appId
        if (!(appId in prev)) {
          const blocked = blocklist.some((wildcard) =>
            API.Domain.matchesWildcard(wildcard, appId),
          )
          const allowed = blocked
            ? false
            : allowlist.some((wildcard) =>
                API.Domain.matchesWildcard(wildcard, appId),
              )
          prev[appId] = {
            appId,
            sessionCount: 0,
            totalDuration: 0,
            blocked,
            allowed,
            adCount: 0,
          }
        }
        prev[appId].sessionCount += 1
        prev[appId].adCount += ads.filter(
          (ad) => ad.sessionId == session.id,
        ).length
        prev[appId].totalDuration += session.endedAt
          ? session.endedAt - session.startedAt
          : 0
        return prev
      },
      {},
    )

    return Object.values(aggregatedSessionsByAppId)
  }

  async getUsageHighlights(
    startDate: number,
    endDate: number,
  ): Promise<UsageHighlights> {
    const usageDatabase = await api.WebUsage.getUsageDatabase()
    const sessions =
      (await usageDatabase.sessionsRepo.findSessions(startDate, endDate)) ?? []
    const ads = await usageDatabase.adsRepo.findAds(startDate, endDate)

    let blocklist: string[] | null = []
    const blockAllWebsites = API.Settings.get('isBlockAllWebsites')
    if (blockAllWebsites) blocklist = null
    else blocklist = getBlocklist()
    const allowlist = await getAllowlist()

    const groupedSessionsByHour = sessions.reduce(
      (prev: Record<string, number>, session) => {
        const hour = new Date(session.startedAt).getHours()
        if (!(hour in prev)) {
          prev[hour] = 0
        }
        prev[hour] += session.endedAt ? session.endedAt - session.startedAt : 0 // Don't count unfinished sessions
        return prev
      },
      {},
    )

    const favoriteHour = Object.keys(groupedSessionsByHour).reduce(
      (a, b) =>
        groupedSessionsByHour[a] > groupedSessionsByHour[b] ? a : Number(b),
      0,
    )

    const aggregatedSessionsByAppId = sessions.reduce(
      (
        prev: {
          [key: string]: {
            count: number
            totalDuration: number
            blocked: boolean
          }
        },
        session,
      ) => {
        const appId = session.appId
        if (!(appId in prev)) {
          prev[appId] = {
            count: 0,
            totalDuration: 0,
            blocked: isHostnameBlocked(appId, blocklist!, allowlist),
          }
        }
        prev[appId].count += 1
        prev[appId].totalDuration += session.endedAt
          ? session.endedAt - session.startedAt
          : 0
        return prev
      },
      {},
    )

    const totalDuration = sessions.reduce((acc, session) => {
      return acc + (session.endedAt ? session.endedAt - session.startedAt : 0)
    }, 0)

    const aggregatedBlockedSessions = Object.values(
      aggregatedSessionsByAppId,
    ).filter((app) => app.blocked)

    const blockedAppCount = aggregatedBlockedSessions.length
    const blockedSessionCount = aggregatedBlockedSessions.reduce(
      (acc, app) => acc + app.count,
      0,
    )

    return {
      sessionCount: sessions.length,
      totalDuration,
      blockedAppCount,
      blockedSessionCount,
      appCount: Object.keys(aggregatedSessionsByAppId).length,
      favoriteHour,
      adCount: ads.length,
    }
  }
}
