<script lang="ts" setup>
import { ref, onMounted } from 'vue'
import DialogScaffold from './DialogScaffold.vue'
import API from '~/core/CoreAPI'

async function enable() {
  API.StayFocusd.setActivated(true)
  visible.value = false
}

const visible = ref(false)

onMounted(async () => {
  let activated = API.Settings.get('activated', true)
  API.Storage.addOnChangeListener((changes) => {
    if (changes.activated) {
      activated = changes.activated.newValue
      visible.value = !activated
    }
  })
  visible.value = !activated
})
</script>

<template>
  <DialogScaffold
    :visible="visible"
    blurred
  >
    <template #body>
      <section
        class="deactivated-section flex flex-col flex-1 items-center justify-center gap-2 px-4 py-4 w-max min-w-[100%] min-h-[120px]"
      >
        <h1 class="font-bold text-center">{{ $t('deactivatedTitle') }}</h1>
        <p class="opacity-50 text-center">{{ $t('deactivatedText') }}</p>

        <iconify-icon
          id="power-icon"
          @click="enable()"
          class="icon-6xl cursor-pointer hover:text-primary text-base-content/50"
          icon="mdi:power"
          width="64"
          height="64"
        />
      </section>
    </template>
  </DialogScaffold>
</template>
