<script lang="ts" setup>
import { ref, onMounted, computed } from 'vue'
import DialogScaffold from './DialogScaffold.vue'
import stayfocusdUrl from '~/public/common/img/logo.svg'
import stayfreeUrl from '~/public/common/img/stayfree.svg'
import puzzleUrl from '~/public/common/img/puzzle.svg'
import crossPlatformPreview from '~/public/common/img/cross-platform-preview.svg'
import API from '~/core/CoreAPI'
import { WEEK } from '~/core/Time'

async function hide() {
  await API.Settings.setAsync({ desktopPromoDismissed: true })
  visible.value = false
}
function onDismiss() {
  hide()
  API.Analytics.event('CLICKED_PROMOTE_DESKTOP_POPUP')
}

function onTry() {
  window.open(
    'https://stayfreeapps.com?download&utm_source=stayfocusd_dialog',
    '_blank',
  )
  hide()
  API.Analytics.event('DISMISSED_PROMOTE_DESKTOP_POPUP')
}

const visible = ref(true)
const promoDismissed = API.Settings.get('desktopPromoDismissed') as
  | boolean
  | undefined
  | null
const firstSensortowerInstallDate = ref(Date.now())

onMounted(async () => {
  let ms = await API.Storage.get('firstSensorTowerInstallDate')
  if (ms) {
    firstSensortowerInstallDate.value = ms
  }
})

const isVisible = computed(() => {
  return (
    visible.value &&
    promoDismissed != true &&
    Date.now() - firstSensortowerInstallDate.value > WEEK
  )
})
</script>

<template>
  <DialogScaffold :visible="isVisible">
    <template #body>
      <div
        data-testid="stayfree-promo-body"
        class="image-container relative flex items-center justify-center p-4 gap-4 rounded bg-neutral overflow-hidden z-0"
      >
        <img
          class="h-8"
          :src="stayfocusdUrl"
        />

        <img
          class="h-8"
          :src="puzzleUrl"
        />

        <div
          class="absolute -right-2 h-full bg-gray-700 -skew-x-12 -z-10 w-[calc(50%+12px)]"
        />

        <img
          class="h-8"
          :src="stayfreeUrl"
        />
      </div>

      <h1 class="text-base-content font-bold text-lg">
        {{ $t('desktopPromoTitle') }}
      </h1>

      <p class="text-base-content">{{ $t('desktopPromoDescription') }}</p>

      <div class="flex items-center justify-center p-2">
        <img
          class=""
          :src="crossPlatformPreview"
        />
      </div>

      <p
        v-html="$t('desktopPromoFooter')"
        class="text-base-content"
      />
    </template>
    <template #buttons>
      <button
        class="btn btn-outline btn-primary"
        @click="onDismiss()"
      >
        {{ $t('maybeLater') }}
      </button>
      <button
        class="btn btn-primary"
        @click="onTry()"
      >
        {{ $t('desktopPromoTry') }}
      </button>
    </template>
  </DialogScaffold>
</template>
