<script lang="ts" setup>
import { computed } from 'vue'

const props = defineProps<{
  /**
   * Whether or not the dialog is visible. Can be used with `useBaseDialogController`.
   */
  visible: boolean
  /**
   * The position of the dialog relative to the element that triggers it.
   *
   * @default "right"
   */
  alignHorizontal?: 'right' | 'left'
}>()

defineEmits<{
  (event: 'dismiss'): void
}>()

/**
 * The alignment from the props, but with defaults applied.
 */
const alignment = computed(() => ({
  x: props.alignHorizontal ?? 'right',
}))
</script>

<template>
  <div class="w-px h-0 overflow-visible relative">
    <!-- Dialog -->
    <transition name="fade-down">
      <div
        v-if="visible"
        class="shadow-xl absolute top-8 rounded-md bg-surface z-30"
        :class="{
          'right-0': alignment.x === 'right',
          'left-0': alignment.x === 'left',
        }"
      >
        <div class="flex flex-col">
          <slot />
        </div>
      </div>
    </transition>

    <!-- Click to dismiss -->
    <div
      v-if="visible"
      @click="$emit('dismiss')"
      class="z-10 fixed inset-0"
    />
  </div>
</template>
