<script lang="ts" setup>
import { Litepicker } from 'litepicker'
import Time, { DAY } from '../core/Time'
import { computed, onMounted, onUnmounted, ref, watch } from 'vue'

const props = defineProps<{
  /**
   * The start of the currently selected date range in MS, inclusive. This should be the beginning of the day that is highlighted
   */
  startDate: number
  endDate: number
}>()
const emits = defineEmits<{
  (event: 'dateSelected', startDate: number, endDate: number): void
}>()

const input = ref<HTMLDivElement>()

const minDate = ref(0)
const maxDate = computed<number>(() => Time.getToday())

const pickerStartDate = computed(() => props.startDate)
// Picker's end date is inclusive, so convert from exclusive to inclusive
const pickerEndDate = computed(() => props.endDate - DAY)

const picker = ref<Litepicker>()
watch(
  [pickerStartDate, pickerEndDate],
  (newPickerStartDate, newPickerEndDate) => {
    picker.value?.setStartDate(newPickerStartDate)
    picker.value?.setEndDate(newPickerEndDate)
  },
)
watch([minDate, maxDate], ([newMinDate, newMaxDate]) => {
  picker.value?.setOptions({
    minDate: newMinDate,
    maxDate: newMaxDate,
  })
})

const pickerOptions = computed(() => ({
  element: input.value!,
  singleMode: false,
  inlineMode: true,
  startDate: pickerStartDate.value,
  endDate: pickerEndDate.value,
  minDate: minDate.value,
  maxDate: maxDate.value,
}))

function showCalendar() {
  picker.value = new Litepicker(pickerOptions.value)
  picker.value.addListener('selected', (pickerStartDate, pickerEndDate) => {
    const newStartDate = pickerStartDate.dateInstance.getTime()
    // Convert from inclusive to exclusive
    const newEndDate = pickerEndDate.dateInstance.getTime() + DAY
    emits('dateSelected', newStartDate, newEndDate)
  })
}
onMounted(showCalendar)

onUnmounted(() => {
  setTimeout(() => picker.value?.destroy(), 200)
})
</script>

<template>
  <div
    ref="input"
    class="SfDateInput select-none"
  />
</template>

<style>
