  --calendar-text: hsl(var(--bc));
  --calendar-text-on-primary: hsl(var(--pc));
  --calendar-text-disabled: hsl(var(--bc) / 0.5);
  --calendar-accent: hsl(var(--p));
  --calendar-accent-highlight: hsl(var(--in));
  --calendar-today: hsl(var(--a));
}

/* See https://github.com/wakirin/Litepicker/blob/master/src/scss/main.scss */
.SfDateInput {
  --litepicker-container-months-color-bg: transparent;
  --litepicker-container-months-box-shadow-color: transparent;

  --litepicker-button-prev-month-color-hover: var(--calendar-accent);
  --litepicker-button-next-month-color-hover: var(--calendar-accent);
  --litepicker-day-color-hover: var(--calendar-accent);
  --litepicker-is-start-color-bg: var(--calendar-accent);
  --litepicker-is-end-color-bg: var(--calendar-accent);
  --litepicker-button-apply-color-bg: var(--calendar-accent);
  --litepicker-button-reset-color-hover: var(--calendar-accent);

  --litepicker-is-in-range-color: var(--calendar-accent-highlight);

  --litepicker-is-today-color: var(--calendar-today);

  --litepicker-button-prev-month-color: var(--calendar-text-disabled);
  --litepicker-button-next-month-color: var(--calendar-text-disabled);
  --litepicker-month-weekday-color: var(--calendar-text-disabled);
  --litepicker-month-week-number-color: var(--calendar-text-disabled);
  --litepicker-is-locked-color: var(--calendar-text-disabled);
  --litepicker-button-cancel-color-bg: var(--calendar-text-disabled);

  --litepicker-month-header-color: var(--calendar-text);
  --litepicker-day-color: var(--calendar-text);
  --litepicker-highlighted-day-color: var(--calendar-text);

  --litepicker-is-start-color: var(--calendar-text-on-primary);
  --litepicker-is-end-color: var(--calendar-text-on-primary);
}
</style>
